.newPassPage {
  display: flex;
  justify-content: center;
}

.newPassContainer {
  display: flex;
  /*flex-direction: column;*/
  /*align-items: center;*/
  /*justify-content: space-around;*/

  width: 413px;
  height: 372px;
  margin-top: 60px;
  padding: 35px;

  font-size: 13px;
  text-align: center;

  border-radius: 2px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  margin: 0;
  padding: 0;
}

.title {
  font-size: 26px;
  font-weight: 600;
  line-height: 32px;
}

.text {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
  text-align: left;
  letter-spacing: 0;

  opacity: 0.5;
}
